import React, { useEffect,useContext, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Navbar, Nav, Container } from 'react-bootstrap';
import { CSSTranstion } from 'react-transition-group';
import './Detail.scss';

function Detail (props){
    let [tap ,setTap] = useState(0)
    let [alert ,setAlert] = useState(true)
    let { id } = useParams();
    let history = useHistory();
    let find = props.shoes.find((f)=>{
      return f.id == id
    })

    useEffect(()=>{
    let 타이머 =  setTimeout(()=>{ 
      setAlert(false)
      },2000)
      return  ()=>{clearTimeout(타이머)}
    },[])

  


  return(
    <div className="container">
          <h2 style={{fontSize: '20px', padding:'20px 0'}}>Detail</h2>

          {
            alert == true
            ?<div className='alert' style={{background:'#e7dddd', padding:'0.5em'}}>
            <p>재고가 얼마 남지않았습니다.</p>
            </div>
            :null
          }

          

          <div className="row">
            <div className="col-md-6">
              <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{find.title}</h4>
              <p>{find.content}</p>
              <p>{find.price} 원</p>
              <p>재고 : {props.재고[id]}</p>
              <button className="btn btn-danger">주문하기</button> 
              &nbsp;
              <button className="btn btn-danger" onClick={(()=>{
                history.goBack();
                // history.push();
              })}>뒤로가기</button> 
            </div>
          </div>
          
          <Nav className='mt-5' variant="tabs" defaultActiveKey="link-0">
            <Nav.Item>
              <Nav.Link eventKey="link-0" onClick={()=>{
                setTap(0)
              }}>Active</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-1" onClick={()=>{
                setTap(1)
              }}>Option 2</Nav.Link>
            </Nav.Item>
          </Nav>
          <TapContent tap = {tap}/>
         
         

      </div> 
  )
  function TapContent(props){

   if(props.tap == 0) {
     return <div>0 번째 내용입니다</div>
   }
   else if(props.tap == 1) {
    return <div>1 번째 내용입니다</div>
  } else if(props.tap == 2) {
    return <div>2 번째 내용입니다</div>
  }
  }
 
}
export default Detail;