/* eslint-disable */
import React, { useEffect,useContext, useState } from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import './App.css';
import Data from './data';
import Cart from './Cart';
import Detail from './Detail';
import { Link, Route, Switch } from 'react-router-dom';
import axios from 'axios';



// let 재고context = React.createContext();

function App() {

  let [shoes, setShoes] = useState(Data);
  let [loding, setLoding] = useState(true);
  let [재고, 재고변경] = useState([10,15,19]);
  let [num, setNum] = useState(0);
  
 
  return (
    <div className="App">

      <Navbar bg="dark" variant="dark">
        <Container>
          <Nav.Link as={Link} to ="/">Navbar</Nav.Link>
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/"> Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">detail</Nav.Link>

          </Nav>
        </Container>
      </Navbar>
    
          
        
      <Switch>
        <Route exact path="/">

          <div className='main'>
            <img style={{ width: '100%' }} src="img/background.jpg" alt="이미지" />
          </div>

          <div className="container">
            
            {/* <재고context.Provider value={재고}> */}
              
            <div className="row">
              {
               shoes.map((a,i)=>{
                return(
                  <Card shoes = {shoes[i]} i={i} setShoes = {setShoes} key={i} 재고 ={재고[i]}/>
                )
                })
              }
             
            </div>

            {/* </재고context.Provider> */}

            {
                loding == false
                ?<div>로딩중...</div>
                :null
            }
         
            <button onClick={()=>{
           
               setLoding(true)

              axios.get('https://codingapple1.github.io/shop/data2.json').
              then((res)=>{
                setShoes([...shoes,...res.data])
              }).catch(()=>{

                setLoding(false)

              })
              
            }} className='btn btn-primary'>더보기</button>
          </div>
        </Route>
        <Route path="/detail/:id">
        <Detail shoes = {shoes} 재고 = {재고}/>
        </Route>

        <Route path="/cart">
          <Cart></Cart>
        </Route>
  
      </Switch>
    


    </div>
  );
}


function Card(props) {

  // let 재고 = useContext(재고context);

  return (
    
    <div className="col-md-4">
     
       <img src={"https://codingapple1.github.io/shop/shoes"+(props.i +1)+".jpg"} width="100%" />
    <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content} </p>
      <p>{props.shoes.price} </p>
    
      {props.재고}
 
 
    </div>
  )
}





export default App;
